import React from "react";
import {Field, reduxForm} from 'redux-form';
import c from './Cart.module.css';
import {Input, Textarea} from "../FormComponents/FormComponents";
import {emptyField, maxLength, number} from "../FormComponents/Validators";

const overSize20 = maxLength(20);
const overSize40 = maxLength(40);
const overSize11 = maxLength(11);

const Form = (props) => {


    return (<div className={c.form}>
        <h3 className={c.total}>       :</h3>
        <form onSubmit={props.handleSubmit}>

            <div className={c.formItem}> :*<Field component={Input} name={'fName'}
                                                          validate={[emptyField, overSize20]}/></div>
            <div className={c.formItem}>:*<Field component={Input} name={'lName'}
                                                    validate={[emptyField, overSize20]}/></div>
            <div className={c.formItem}>:*<Field component={Input} name={'number'}
                                                        validate={[emptyField, overSize11, number]}/></div>
            <div className={c.formItem}> :<Field component={Input} name={'email'}
            /></div>
            <div className={c.formItem}>:*<Field component={Textarea} name={'address'}
                                                       validate={[emptyField, overSize40]}
            /></div>
            <button className={c.but}></button>
        </form>
        <p className={c.total}>* -  '  </p>
    </div>)
}

const CartForm = reduxForm({
    form: 'cart'
})(Form);

export default CartForm;

