import React from "react";
import c from './Bottom.module.css';

const Bottom = (props) => {
    return (<div className={c.bottom}>
        <div className={c.flex}>
            <div>
                <p> . 0 800 000 000 () </p>
                <p> . +38 (000) 000 000 0 </p>
                <p> . +38 (000) 000 000 0 </p>
            </div>
            <div>
                <h3>  : </h3>
                <p>  - : 10:00- 20:00  </p>
                <p> : 12:00 - 18:00  </p>
            </div>
        </div>
        <div className={c.year}>2015 - 2020  </div>
    </div>);
}

export default Bottom;